<template>
  <div class="modal-dialog">
      <div class="modal-content border-0">
        <div class="modal-header bg-danger text-white">
          <h5 class="modal-title" v-if="temp.category" id="delProductModalLabel"><span></span></h5>
          <h5 class="modal-title" v-if="temp.create_at" id="delOrderModalLabel"><span></span></h5>
          <h5 class="modal-title" v-if="temp.code" id="delCouponLabel"><span></span></h5>
          <button class="btn-close" type="button" data-bs-dismiss="modal" aria-label="Close"></button>
        </div>
        <div class="modal-body" v-if="temp.category"> <strong class="text-danger"> {{ temp.title }}</strong> ()</div>
        <div class="modal-body" v-if="temp.create_at"> <strong class="text-danger"> {{ temp.create_at }}</strong> ()</div>
        <div class="modal-body" v-if="temp.code"> <strong class="text-danger"> {{ temp.title }}</strong> ()</div>
        <div class="modal-footer">
          <button class="btn btn-outline-secondary" type="button" data-bs-dismiss="modal"></button>
          <button class="btn btn-danger" type="button" v-if="temp.category" @click="delProduct(temp.id)"></button>
          <button class="btn btn-danger" type="button" v-if="temp.create_at" @click="delOrder(temp.id)"></button>
          <button class="btn btn-danger" type="button" v-if="temp.code" @click="delCoupon(temp.id)"></button>
        </div>
      </div>
    </div>
</template>

<script>
export default {
  props: ['temp', 'delModel'],
  methods: {
    // 
    delProduct (id) {
      this.axios
        .delete(`${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/product/${id}`)
        .then(res => {
          alert(res.data.message)
          this.delModel.hide()
          this.$emit('getProducts')
        })
        .catch(error => {
          alert(error.response.data.message)
        })
    },
    // 
    delOrder (id) {
      this.axios.delete(`${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/order/${id}`)
        .then(res => {
          alert(res.data.message)
          this.delModel.hide()
          this.$emit('getOrders')
        })
        .catch(error => {
          alert(error.response.data.message)
        })
    },
    // 
    delCoupon (id) {
      this.axios.delete(`${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/coupon/${id}`)
        .then(res => {
          alert(res.data.message)
          this.delModel.hide()
          this.$emit('getCoupons')
        })
        .catch(error => {
          alert(error.response.data.message)
        })
    }
  }
}
</script>
